/*MEDIA QUERIES*/

@mixin media($size) {
    @if $size ==xs {
        @media (max-width: 578px) {
            @content;
        }
    }

    @else if $size ==sm {
        @media (min-width: 579px) {
            @content;
        }
    }

    @else if $size ==md {
        @media (min-width:768px) {
            @content;
        }
    }

    @else if $size ==lg {
        @media (min-width: 992px) {
            @content;
        }
    }
    @else if $size ==xl {
        @media (min-width: 1200px) {
            @content;
        }
    }
    @else if $size ==xxl {
        @media (min-width: 1400px) {
            @content;
        }
    }

}